// import RadioButton from "./RadioButton";
import React, { useState, useEffect } from "react";
import Grade from "./Grade";
import Avatar from "react-avatar";

import { HiPlusCircle } from "react-icons/hi";
import Praise from "./Praise";
import Correct from "./Correct";
import UserAvatar from "./Test/UserAvatar";
import ButtonNavigationLeft from "./ButtonNavigationLeft";
import ButtonNavigationRight from "./ButtonNavigationRight";

function GradeDetail(props) {
  const { student } = props;
  const [selectedGrade, setSelectedGrade] = useState(null);
  const [comment, setComment] = useState("");
  const [hasNextSkill, setHasNextSkill] = useState(false);
  // console.log('hasNextSkill: ', hasNextSkill);
  const [hasPrevSkill, setHasPrevSkill] = useState(false);
  // console.log('hasPrevSkill: ', hasPrevSkill);
  const [hasNextSubSkill, setHasNextSubSkill] = useState(false);
  // console.log('hasNextSubSkill: ', hasNextSubSkill);
  const [hasPrevSubSkill, setHasPrevSubSkill] = useState(false);
  const [hasNextStudent, setHasNextStudent] = useState(false);
  console.log('hasNextStudent: ', hasNextStudent);
  const [hasPrevStudent, setHasPrevStudent] = useState(false);
  console.log('hasPrevStudent: ', hasPrevStudent);

  const studentGrade = props.performances.filter(
    (perf) =>
      perf.user_id === student.id && perf.skill_id === props.detailSkill.id
  );

  // studentGrade[0] &&
  //   console.log("________comment studentGrade: ", studentGrade[0].comment);

  const scoreName = studentGrade[0]
    ? props.scores.find((score) => score.id == studentGrade[0]?.score_id)?.name
    : props.scores[props.scores.length - 1]?.name;

  useEffect(() => {
    setSelectedGrade(scoreName);
    setComment(studentGrade[0]?.comment ? studentGrade[0].comment : "");
  }, [
    props.performances,
    props.student,
    props.skillSet,
    props.detailSkill,
    scoreName,
    studentGrade,
  ]);

  useEffect(() => {
    setHasNextStudent(props.currentStudentIndex < props.studentsLength-1);
    console.log('has props.studentsLength: ', props.studentsLength);

    setHasPrevStudent(props.currentStudentIndex > 0);
  }, [props.studentsLength, props.currentStudentIndex]);

  useEffect(() => {
    setHasNextSkill(props.currentSkillIndex < props.skillSetsLength-1);
    setHasPrevSkill(props.currentSkillIndex > 0);
  }, [props.skillSetsLength, props.currentSkillIndex]);

  useEffect(() => {
    setHasNextSubSkill(props.currentSubSkillIndex < props.subSkillSetsLength-1);
    console.log('has props.subSkillSetsLength: ', props.subSkillSetsLength-1);
    console.log('has currentSubSkillIndex: ', props.currentSubSkillIndex);
    // console.log('has !!props.subSkillSets: ', props.subSkillSets);
    setHasPrevSubSkill(props.currentSubSkillIndex > 0);
  }, [props.subSkillSetsLength, props.currentSubSkillIndex]);

  const addComment = (addedComment) => {
    const newComment =
      comment === "" ? addedComment : comment + " " + addedComment;
    setComment(newComment + " ");
  };
  const submitPerformance = () => {
    props.submitPerformance(
      student.id,
      props.detailSkill.id,
      selectedGrade,
      comment
    );
  };
  console.log("props.praises: ", props.praises);
  console.log("props.corrects: ", props.corrects);
  return (
    <>
      {student && (
        <div
          className="fixed z-50 inset-0 overflow-y-auto  xl:mt-0"
          aria-labelledby="modal-title"
          role="dialog"
          aria-modal="true"
          key={props.student?.id + props.detailSkill?.id}
        >
          <div className="flex items-end justify-center min-h-screen pt-4 px-4 pb-20 text-center sm:block sm:p-0">
            <div
              className="fixed inset-0 bg-gray-500 bg-opacity-75 transition-opacity"
              aria-hidden="true"
              onClick={props.closeDetails}
            ></div>

            <span
              className="hidden sm:inline-block sm:align-middle sm:h-screen"
              aria-hidden="true"
            >
              &#8203;
            </span>

            <div className="inline-block align-bottom bg-white rounded-lg text-left overflow-hidden shadow-xl transform transition-all sm:my-8 sm:align-middle sm:max-w-lg sm:w-full -mt-4">
              <div className="bg-white px-4 pt-5 pb-4 sm:p-6 sm:pb-4">
                <div className="sm:items-start">
                  <div className="pl-2 flex flex-row justify-around  w-full bg-martialup-400 mb-1 py-1 	 items-center	rounded-lg border-b border-gray-200	 ">
                    <ButtonNavigationLeft clickHandler={props.prevSkill} isActive={hasPrevSkill}/>
                    <span className="text-center text-sm   mb-1 px-2 py-1 rounded-b w-full">
                      {/* <span className="text-left text-sm   mb-0 px-2 py-0 rounded-b"> */}
                      {props.skillSet?.name}
                    </span>
                    <ButtonNavigationRight clickHandler={props.nextSkill} isActive={hasNextSkill} />
                  </div>
                  <div className="pl-2 py-2 flex flex-row justify-around  w-full	 bg-martialup-900 text-gray-50 items-center	rounded-lg border-b border-gray-200  ">
                    <ButtonNavigationLeft
                      clickHandler={props.prevSubSkill}
                      isDark={true}
                      isActive={hasPrevSubSkill}
                    />
                    <span className="text-center text-xs xs:text-base   w-full bg-martialup-900 text-gray-50 mb-1 shadow-md px-2 py-1 rounded-b">
                      {props.detailSkill?.name}
                    </span>
                    <ButtonNavigationRight clickHandler={props.nextSubSkill}  isDark={true} isActive={hasNextSubSkill} />
                  </div>
                  <div className="flex items-center ">
                    <div className="flex items-center  justify-center bg-gray-50 w-full rounded-lg align-middle p-2 border-b-2 border-gray-200">
                      <ButtonNavigationLeft clickHandler={props.prevStudent} isActive={hasPrevStudent}/>

                      <div
                        className={`flex items-center  justify-center  w-full  align-middle ${
                          window.innerWidth < 640 ? "" : "p-2"
                        } `}
                      >
                        <div className="mr-4 flex flex-col align-middle justify-center ">
                          {student.avatar ? (
                            <UserAvatar detail={true} url={student.avatar} />
                          ) : (
                            <Avatar
                              name={student.name + " " + student.lastName}
                              round
                              maxInitials={2}
                              size={
                                props.hasFinished
                                  ? "30"
                                  : window.innerWidth < 640
                                  ? "50"
                                  : "100"
                              }
                              textSizeRatio={props.hasFinished ? 1.9 : 1.7}
                            />
                          )}
                        </div>
                        <div className="mr-4 flex flex-col 	w-full ">
                          <span className="text-left xs:text-2xl text-martialup-900 mb-1">
                            {student.name + " " + student.last_name}
                            <span className="inline ml-2 text-gray-400">
                              {student.age}
                            </span>
                          </span>
                          <div className=" flex flex-row   w-full">
                            <span
                              className={`text-left ${
                                window.innerWidth < 640 ? "mr-1" : "mr-10"
                              } text-xs xs:text-base text-gray-500 mb-1 `}
                            >
                              {props.groupName}
                            </span>
                            <span
                              className="text-left  text-xs xs:text-base  rounded-lg  border-gray-50 px-2 font-extrabold  shadow-md"
                              style={{
                                backgroundColor: student.rank?.background_color,
                                color: student.rank?.foreground_color,
                              }}
                            >
                              {student.rank?.name}
                            </span>
                          </div>
                        </div>
                      </div>
                      <ButtonNavigationRight clickHandler={props.nextStudent} isActive={hasNextStudent}/>
                    </div>
                  </div>

                  <div className="flex items-center mt-2 ">
                    <div className="flex items-center bg-gray-50 w-full rounded-lg align-middle p-2 border-b-2 border-gray-200">
                      <div className="mr-4 flex flex-row justify-between	 w-full		">
                        <Grade
                          grade="A"
                          detailView={true}
                          hasMessage={"none"}
                          selectedGrade={selectedGrade}
                          onClick={() => setSelectedGrade("A")}
                        />
                        <Grade
                          grade="B"
                          detailView={true}
                          hasMessage={"none"}
                          selectedGrade={selectedGrade}
                          onClick={() => setSelectedGrade("B")}
                        />
                        <Grade
                          grade="C"
                          detailView={true}
                          hasMessage={"none"}
                          selectedGrade={selectedGrade}
                          onClick={() => setSelectedGrade("C")}
                        />
                        <Grade
                          grade="D"
                          detailView={true}
                          hasMessage={"none"}
                          selectedGrade={selectedGrade}
                          onClick={() => setSelectedGrade("D")}
                        />
                        <Grade
                          grade="E"
                          detailView={true}
                          hasMessage={"none"}
                          selectedGrade={selectedGrade}
                          onClick={() => setSelectedGrade("E")}
                        />
                        <Grade
                          grade="N/A"
                          detailView={true}
                          hasMessage={"none"}
                          selectedGrade={selectedGrade}
                          onClick={() => setSelectedGrade("N/A")}
                        />
                      </div>
                    </div>
                  </div>
                  <div className="flex items-center mt-2 ">
                    <div className="flex items-center bg-gray-50 w-full rounded-lg align-middle p-2 border-b-2 border-gray-200">
                      <div className="flex flex-col w-full">
                        <div className="mr-4 flex flex-row justify-between	 w-full		">
                          <div className="mr-4 flex flex-row justify-between	 w-full		mb-3">
                            <span className="text-xs text-gray-500">
                              Praise
                            </span>
                            <span
                              className=" text-2xl text-green-400 cursor-pointer select-none "
                              onClick={props.openPraiseDetails}
                            >
                              <HiPlusCircle />
                            </span>
                          </div>
                        </div>
                        <div className="mr-4 flex overflow-scroll   ">
                          <div className="flex  lg:flex-wrap max-h-12">
                            {props.praises?.map((praise) => {
                              return (
                                <Praise
                                  selected={true}
                                  text={praise.name}
                                  onClick={() => addComment(praise.name)}
                                  key={praise.name}
                                />
                              );
                            })}
                          </div>
                        </div>
                      </div>
                    </div>
                  </div>
                  <div className="flex items-center mt-2 ">
                    <div className="flex items-center bg-gray-50 w-full rounded-lg align-middle p-2 border-b-2 border-gray-200">
                      <div className="flex flex-col w-full">
                        <div className="mr-4 flex flex-row justify-between	 w-full		">
                          <div className="mr-4 flex flex-row justify-between	 w-full		mb-3">
                            <span className="text-xs text-gray-500">
                              Corrections
                            </span>
                            <span
                              className=" text-2xl text-red-400 cursor-pointer "
                              onClick={props.openCorrectDetails}
                            >
                              <HiPlusCircle />
                            </span>
                          </div>
                        </div>
                        <div className="mr-4 flex overflow-scroll  hide-scroll-bar ">
                          <div className="flex lg:flex-wrap max-h-16">
                            {props.corrects?.map((correct) => {
                              return (
                                <Correct
                                  selected={true}
                                  text={correct.name}
                                  key={correct.name}
                                  onClick={() => addComment(correct.name)}
                                />
                              );
                            })}
                          </div>
                        </div>
                      </div>
                    </div>
                  </div>
                  <div className="flex items-center mt-2 ">
                    <div className="flex  flex-col  bg-gray-50 w-full rounded-lg align-middle p-2 border-b-2 border-gray-200">
                      <div className="">
                        <textarea
                          rows="2"
                          name="message"
                          id="message"
                          placeholder="Your Comment"
                          className="w-full px-3 py-2 placeholder-gray-300 border border-gray-300 rounded-md focus:outline-none focus:ring focus:ring-indigo-100 focus:border-indigo-300 dark:bg-gray-700 dark:text-white dark:placeholder-gray-500 dark:border-gray-600 dark:focus:ring-gray-900 dark:focus:border-gray-500"
                          required
                          value={comment}
                          onChange={(e) => setComment(e.target.value)}
                        ></textarea>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
              <div className="px-4 mb-2 sm:px-6 sm:flex sm:flex-row-reverse">
                <button
                  onClick={submitPerformance}
                  type="button"
                  className="w-full inline-flex justify-center rounded-md border border-transparent shadow-sm px-4 py-2 bg-green-600 text-base font-medium text-white hover:bg-green-900 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-red-500 sm:ml-3 sm:w-auto sm:text-sm"
                >
                  Submit
                </button>
                <button
                  onClick={props.closeDetails}
                  type="button"
                  className="mt-3 w-full inline-flex justify-center rounded-md border border-gray-300 shadow-sm px-4 py-2 bg-white text-base font-medium text-gray-700 hover:bg-gray-400 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-indigo-500 sm:mt-0 sm:ml-3 sm:w-auto sm:text-sm"
                >
                  Cancel
                </button>
              </div>
            </div>
          </div>
        </div>
      )}
    </>
  );
}

export default GradeDetail;
