const store = configureStore();

function App() {
  return (
    <PusherProvider {...config}>
      <Provider store={store}>
        <Router>
          <Switch>
            <Route path="/login" component={NewLoginPage} />
            <ProtectedRoute
              exact={true}
              path="/gradings"
              component={TestList}
            />
            <ProtectedRoute path="/" exact={true} component={TestList} />
            <ProtectedRoute
              path="/test/:page"
              // exact={true}
              component={Test}
            />
          </Switch>
        </Router>
      </Provider>
    </PusherProvider>
  );
}

export default App;
